export default {
	data() {
		return {
			uid: '',
			form: {
				name: '',
				isFamily: '',
				fagId: '',
				isSelling: true
			},
			rules: {
				name: {
					required: '',
					message: '请输入商品名称'
				}
			}
		};
	},
	onLoad(option) {
		this.uid = option.id;
	}
};
